"use client"; // needed for Vanta.js and dynamic effects

import { useEffect, useRef } from "react";
import * as THREE from "three";
import VANTA from "vanta/dist/vanta.globe.min";

export default function Home() {
  const vantaRef = useRef<HTMLDivElement>(null);
  const vantaEffect = useRef<any>(null);

  useEffect(() => {
    if (!vantaEffect.current && vantaRef.current) {
      vantaEffect.current = VANTA.GLOBE({
        el: vantaRef.current,
        THREE,
        mouseControls: true,
        touchControls: true,
        gyroControls: false,
        minHeight: 200.0,
        minWidth: 200.0,
        scale: 1.0,
        scaleMobile: 1.0,
        color: 0x6366f1,
        backgroundColor: 0x0,
      });
    }
    return () => {
      if (vantaEffect.current) vantaEffect.current.destroy();
    };
  }, []);

  return (
    <div ref={vantaRef} className="relative min-h-screen bg-gray-900 text-white">
      {/* Navigation */}
      <nav className="glass-effect fixed top-4 left-1/2 transform -translate-x-1/2 w-11/12 max-w-6xl mx-auto px-6 py-3 flex justify-between items-center z-50">
        <div className="flex items-center space-x-2">
          <i data-feather="cpu" className="text-indigo-400"></i>
          <span className="text-white font-bold text-xl">Neon Nexus</span>
        </div>
        <div className="hidden md:flex space-x-8">
          {["Home", "Shop", "Builds", "Components", "Games"].map((link) => (
            <a key={link} href="#" className="text-white hover:text-indigo-300 transition">
              {link}
            </a>
          ))}
        </div>
        <div className="flex items-center space-x-4">
          <button className="glass-effect px-4 py-2 rounded-full text-white flex items-center space-x-2">
            <i data-feather="user" className="w-4 h-4"></i>
            <span>Login</span>
          </button>
          <button className="relative">
            <i data-feather="shopping-cart" className="text-white"></i>
            <span className="absolute -top-2 -right-2 bg-indigo-500 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center">
              3
            </span>
          </button>
        </div>
      </nav>

      {/* Hero Section */}
      <section className="hero-gradient min-h-screen flex items-center justify-center px-4 pt-24 pb-16">
        <div className="glass-effect max-w-4xl mx-auto p-8 md:p-12 text-center">
          <h1 className="text-4xl md:text-6xl font-bold text-white mb-6">
            Power Your Gaming Dreams
          </h1>
          <p className="text-xl text-white/80 mb-10">
            Ultimate gaming PCs, components, and titles curated for peak performance.
          </p>
          <div className="flex flex-wrap justify-center gap-4">
            <button className="glass-effect px-8 py-3 rounded-full text-white font-medium flex items-center space-x-2 hover:scale-105 transition">
              <span>Shop Now</span>
              <i data-feather="arrow-right" className="w-4 h-4"></i>
            </button>
            <button className="bg-white/10 hover:bg-white/20 px-8 py-3 rounded-full text-white font-medium border border-white/20">
              Custom Build
            </button>
          </div>
        </div>
      </section>
    </div>
  );
}
